




























































































































































import {Component, Vue} from "vue-property-decorator";
import {HttpService} from "@/services/ApiService";
import {serviceUpdatedAll, shippingUpdatedAll} from "@/graphql/subscription";

@Component
export default class ConfigService extends Vue{
  public services: any[] = [];

  public serviceData = {
    id: 0,
    name: "",
    descripcion: "",
    wallpaper: '',
    order: 0,
    delay: 0,
    hour_init: 0,
    hour_fin: 0,
    hour_error: "",
    error_message_programming: "",
  }

  getServices(){
    HttpService.get(`services/table` ,
        this.setServices)
  }
  setServices(data: any){
    console.log(data)
    this.services = data.data;
  }

  openDialog(index: number){
    let ser = this.services[index];

    this.serviceData ={
      id: ser.id,
      name: ser.name,
      descripcion: ser.descripcion,
      delay: ser.delay,
      order: ser.order,
      wallpaper: ser.wallpaper,
      hour_init: ser.hour_init,
      hour_fin: ser.hour_fin,
      hour_error: ser.hour_error,
      error_message_programming: ser.error_message_programming,
    }

    // @ts-ignore
    this.$refs.modal.open()
  }

  updateService(){
    HttpService.put(`services/table` ,this.serviceData,
        this.setItem)

    // @ts-ignore
    this.$refs.modal.close()
  }

  setItem(data:any){
    let delivery = data.data;
    let index = this.services.findIndex(e => e.id == delivery.id);
    if(index != undefined && index > -1){
      //this.deliveries.splice(index, 1, delivery)
      console.log(index);
      this.$set(this.services, index, delivery)
    }
  }

  created(){
    this.getServices();


    const observer = this.$apollo.subscribe({
      query: serviceUpdatedAll,
    })
    observer.subscribe({
      next (data) {
        console.log(data)
      },
      error (error) {
        console.log(error)
      },
    })
  }

}
